.rankMedal-icon{
  position: absolute;
  width: 15%;
  padding-right: 1%;
  padding-top: 1%;
}

.rankMedal-star{
  width: 15%;
  height: 5%;
}

@media only screen and (max-width: 600px) {
  .rankMedal-icon{
    position: absolute;
    width: 50%;
    padding-right: 1%;
    padding-top: 3%;
  }
  
  .rankMedal-star{
    width: 55%;
    height: 5%;
  }
}

.col-title{
    font-family: 'Indie Flower', cursive;
    text-align: center;
}

.header{
    background: rgb(2,0,36);
    background: linear-gradient(90deg, rgba(2,0,36,0.15449929971988796) 0%, rgba(0,0,0,0.19091386554621848) 100%);
    text-align: center;
    padding-top: 1%;
    padding-bottom: 1%;
}

a {
