.rankMedal-icon{
  position: absolute;
  width: 15%;
  padding-right: 1%;
  padding-top: 1%;
}

.rankMedal-star{
  width: 15%;
  height: 5%;
}

@media only screen and (max-width: 600px) {
  .rankMedal-icon{
    position: absolute;
    width: 50%;
    padding-right: 1%;
    padding-top: 3%;
  }
  
  .rankMedal-star{
    width: 55%;
    height: 5%;
  }
}
